import Header from "./Header";
import { NavItem } from "./interfaces";

interface Props {
    title?: string;
}

const pages: NavItem[] = [
    {
        name: "Getting Started",
        href: "/getting_started",
    },
    {
        name: "API",
        href: "/api",
    }
];

const BaseLayout: React.FunctionComponent<Props> = ({
    children,
    title = "intermix",
}) => (
    <div>
        <header>
            <Header title={title} links={pages} />
        </header>
        {children}
        <footer>
            <p>Hello!</p>
        </footer>
    </div>
)

export default BaseLayout;
